:root {
    /* Color var */
    --text-color-1: #fff;
    --text-color-2: #FFC300;
    --text-color-3: #6FFFE9;
    --text-color-4: #ffffff83;
    --background-color-1: #000814;
    --background-color-2: #001D3D;
    --background-color-3: #003566;

    --button-color: #003566;

    /* Font var */
    --font-family-1: 'Poppins', sans-serif;
    
    --big-size: 25px;
    --mid-size: 20px;
    --small-size: 10px;
    --text-size: 16px;
}

body {
    background-color: var(--background-color);
}